  /* ============================================= */
  /*              Buttons                          */
  /* ============================================= */
  
  .btn
    {
      margin-top: 15px;
      width: 100%;
      color: #fff;
      border: none;
      padding: 10px 0;
      text-transform: uppercase;
      border-radius: 5px;
      
      @media (min-width: 768px)
      {
          padding: 10px 30px;
          
          & + & 
            {
              margin-left: 10px;
            }
      }
    }
  
  .default {
    background-color: #51ddfc;
  }
  
  .error {
    background-color: #e4757a;
  }
  
  .info {
    background-color: #927bc1;
  }
  
  .success {
    background-color: #63cc82;
  }
  
  .warning {
    background-color: #fd7856;
  }