/* ============================================= */
/*              Media Queries                    */
/* ============================================= */
@media (min-width: 768px) {
  
    .container {
      max-width: 1400px;
      @include img;
    }
      
    .centered {
      @include img;
    }
      
    .row {
      flex-direction: row;
    }
      
      [class^=col] {
        display: flex;
        justify-content: center;
        margin-top: 0;
        align-items: center;
      }
      
      [class^=col] + [class^=col] {
        margin-left: 10px;
      }
      
      .logo
        {
          margin-left: initial;
          margin-right: initial;
        }
      }